/**
 * BLOOM token interface
 */
import { EthEngine } from "../eth/eth-engine";
import { TokenConfig } from "../config/tokens/tokenconfig";
import { TokenConfigMain } from "../config/tokens/tokenconfigmain";
import {DEX} from "../eth/tokens/dex";

export class LunyrTokenTestnet extends DEX {
  constructor(ethEngine: EthEngine) {
    super(TokenConfig.Lunyr.contractAddress, ethEngine);
  }
}

export class LunyrTokenMainnet extends DEX  {
  constructor(ethEngine: EthEngine) {
    super(TokenConfigMain.Lunyr.contractAddress, ethEngine);
  }
}


